.skillsBlock {
    min-height: 100vh;
    background-color: #c2e8f1;
}

.skillsContainer {
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;
    box-sizing: border-box;
}

.blockTitle {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 5px 0;

}

.skills {
    /*border: 1px solid;*/
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

}